import React, { useEffect } from "react";
import contactImage from "../images/contact.jpg";
import logo from "../images/logo.jpg";
import Aos from "aos";
import "aos/dist/aos.css";

const Contact = () => {
  useEffect(() => {
    Aos.init();
  });
  return (
    <div className="contact">
      <div className="container">
        <div className="row">
          <div className="col-lg-4 col-md-4 col-sm-12">
            <div data-aos="flip-left" className="contact-image">
              <img className="img-fluid" src={contactImage} alt="" />
            </div>
          </div>
          <div className="col-lg-4 col-md-4 col-sm-6">
            <div className="ways-contact">
              <div className="info-number">
                <div className="number">
                  <i class="fas fa-phone-alt"></i>
                  <span>+2 010 300 89 100</span> <br />
                </div>
                <div className="info">
                  <i class="fas fa-envelope"></i>
                  <span>info@goodcrafteg.com</span>
                </div>
              </div>
              <div className="social">
                <a href="https://www.facebook.com/goodcrafteg" target="_blank">
                  <i class="fab fa-facebook"></i>
                </a>
                <a
                  href="https://www.linkedin.com/company/goodcrafteg"
                  target="_blank"
                >
                  <i class="fab fa-linkedin"></i>
                </a>
                <a href="https://www.instagram.com/goodcrafteg" target="_blank">
                  <i class="fab fa-instagram"></i>
                </a>
              </div>
            </div>
          </div>
          <div className="col-lg-4 col-md-4 col-sm-6">
            <div className="logo-contact">
              <div className="logo">
                <img className="img-fluid" src={logo} alt="" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
