import React from "react";

const ProjectItem = (props) => {
  return (
    <div className="project-item">
      <div className="item">
        <div className="row">
          <div className="col-lg-6 col-md-6 col-sm-12">
            <div
              id={`carouselExampleControls${props.num}`}
              class="carousel slide"
              data-bs-ride="carousel"
            >
              <div class="carousel-inner">
                <div class="carousel-item active">
                  <img src={props.src1} class="d-block w-100" alt="..." />
                </div>
                {props.src2 !== null ? (
                  <div class="carousel-item">
                    <img src={props.src2} class="d-block w-100" alt="..." />
                  </div>
                ) : null}
                {props.src3 !== null ? (
                  <div class="carousel-item">
                    <img src={props.src3} class="d-block w-100" alt="..." />
                  </div>
                ) : null}
                {props.src4 !== null ? (
                  <div class="carousel-item">
                    <img src={props.src4} class="d-block w-100" alt="..." />
                  </div>
                ) : null}
                {props.src5 !== null ? (
                  <div class="carousel-item">
                    <img src={props.src5} class="d-block w-100" alt="..." />
                  </div>
                ) : null}
              </div>
              {props.src2 === null ? null : (
                <div>
                  <button
                    class="carousel-control-prev"
                    type="button"
                    data-bs-target={`#carouselExampleControls${props.num}`}
                    data-bs-slide="prev"
                  >
                    <span
                      class="carousel-control-prev-icon"
                      aria-hidden="true"
                    ></span>
                    <span class="visually-hidden">Previous</span>
                  </button>
                  <button
                    class="carousel-control-next"
                    type="button"
                    data-bs-target={`#carouselExampleControls${props.num}`}
                    data-bs-slide="next"
                  >
                    <span
                      class="carousel-control-next-icon"
                      aria-hidden="true"
                    ></span>
                    <span class="visually-hidden">Next</span>
                  </button>
                </div>
              )}
            </div>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12">
            <div
              className="description"
              style={{ paddingTop: props.num < 9 ? "45px" : "" }}
            >
              <p>
                <strong> Projcet : </strong> <span>{props.project}</span>
              </p>
              <p>
                <strong> Client : </strong> <span>{props.client}</span>
              </p>
              <p>
                <strong>Location : </strong>
                <span>{props.location}</span>
              </p>
              <p>
                <strong> Consultant : </strong> <span>{props.consultant}</span>
              </p>
              <p>
                <strong> Scope of work : </strong>
                {props.num < 9 ? (
                  <span>{props.scope}</span>
                ) : (
                  <div className="text-white scope-types">
                    <ol type="1" style={{ padding: "0", paddingLeft: "14px" }}>
                      <div className="row">
                        <div className="col-lg-4">
                          <li>
                            Design
                            <ol type="A" style={{ padding: "0" }}>
                              <li>{props.desginType1}</li>
                              <li>{props.desginType2}</li>
                              {props.desginType3 !== null ? (
                                <li>{props.desginType3}</li>
                              ) : null}
                            </ol>
                          </li>
                        </div>
                        <div className="col-lg-4">
                          <li>Project Management</li>
                        </div>
                        <div className="col-lg-4">
                          <li>
                            Construction
                            <ol type="A" style={{ padding: "0" }}>
                              <li>{props.constructionType1}</li>
                              <li>{props.constructionType2}</li>
                              {props.constructionType3 !== null ? (
                                <li>{props.constructionType3}</li>
                              ) : null}
                            </ol>
                          </li>
                        </div>
                      </div>
                    </ol>
                  </div>
                )}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProjectItem;
