import React from "react";
import "../../css/navbar.css";
import logo from "../../images/logo.jpg";

const Navbar = () => {
  return (
    <div className="my-navbar">
      <nav className="navbar navbar-expand-lg navbar-light">
        <div className="container-fluid">
          <a className="navbar-brand" href="/">
            <img
              className="img-fluid"
              style={{ width: "130px", height: "50px", backgroundColor: "" }}
              src={logo}
              alt=""
            />
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span
              style={{ color: "red" }}
              className="navbar-toggler-icon"
            ></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav ms-auto">
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#about">
                  ABOUT US
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#values">
                  VALUES
                </a>
              </li>

              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#expertise">
                  OUR EXPERTISE
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#services">
                  SERVICES
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#types">
                  TYPES OF PROJECTS
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#projects">
                  PROJECTS
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#contact">
                  CONTACT US
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
