import React from "react";
import Navbar from "./layouts/Navbar";
import Header from "./Header";
import Service from "./Service";
import Expertise from "./Expertise";
import Contact from "./Contact";
import Project from "./Project";
import Values from "./Values";
import About from "./About";
import SectionName from "./SectionName";
import Types from "./Types";

import "../css/app.css";

const App = () => {
  return (
    <div>
      <Navbar />
      <Header />
      <div id="about">
        <SectionName part1="About " part2="us" />
      </div>
      <About />

      <div id="values">
        <SectionName part1="" part2="Values" />
      </div>
      <Values />

      <div id="expertise">
        <SectionName part1="Our " part2="Expertise" />
      </div>
      <Expertise />

      <div id="services">
        <SectionName part1="Our " part2="Services" />
      </div>
      <Service />

      <div id="types">
        <SectionName part1="Types" part2=" of Projects" />
      </div>

      <Types />

      <div id="projects">
        <SectionName part1="Our " part2="Projects" />
      </div>
      <Project />

      <div id="contact">
        <SectionName part1="Contact " part2="us" />
      </div>
      <Contact />
    </div>
  );
};

export default App;
